*{box-sizing:border-box}
html,body {
  margin: 0;
  padding:0;
  overflow:hidden;
  max-width: 100vw;
  max-height:100vh;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.App {
  width: 100vw;
  height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  text-align: center;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.logo{position: fixed;  background-size: contain; background-position: 50% 50%; background-repeat:no-repeat; top: 50%; left: 50%; font-size: 4.7vh; width: 6em; height:6em;  margin-left:-3em; margin-top:-3em}
@media (orientation:portrait){.logo{font-size: 4.7vw;}}
svg{max-width: 100vw}